import React from "react"
import Layout from "../pages/layout"
import "../styles/singleProject.scss.css"
import { Helmet } from "react-helmet"
import { FaGithub } from 'react-icons/fa';
import Footer from "../components/footer"

export default class SingleProject extends React.Component {

  constructor(props) {
    super(props);
    console.log(this.props);
  }

  renderTechs = () => {
    return this.props.data.dataJson.techs.map(e => {
      return (
        <div className="single_project_tech" key={Math.random()}>
          <a href={e.url} aria-label={e.description} >
            <img src={e.pic_url} alt={e.name} title={e.name} />
          </a>
        </div>
      )
    })
  }

  render() {
    let d = this.props.data.dataJson
    return (
      <Layout>
        <Helmet>
        <title>{d.description}</title>
        <meta name="description" content={d.description}/>
        <link rel="canonical" href={this.props.location.href}/>
        </Helmet>
        <div className='single_project_container container'>
          {/*<h1>{post.frontmatter.title}</h1>*/}
          {/*<div dangerouslySetInnerHTML={{ __html: post.html }} />*/}

          <br/>

          <header className="project_description">
            {d.description}
          </header>

          <br/>
          <div className='project_techs'>
            {this.renderTechs()}
          </div>

          <br/>
          <div className="project_media">
            <img src={d.media} alt={d.description} aria-label={"image of " + d.name}/>
          </div>

          <div className="project_story">
            {d.story}
          </div>

          <div className="visit_git">
            You can visit the repository of the project on GitHub
          </div>

          <div className="visit_codepen">
            You can visit the code of the project on Codepen
          </div>

          <div className="visit_heroku">
            You can browse the contents of the website on Heroku
          </div>



        </div>
        <Footer/>
      </Layout>
    )
  }

  // const post = data.dataJson
  // console.log(data);

}
export const query = graphql`
    query($slug: String!) {
        dataJson(fields: { slug: { eq: $slug } }) {
            id,
            date,
            description,
            git_link,
            name,
            story,
            web_link,
            media,
            media_pre,
            techs {
                id,
                description,
                name,
                pic_url,
                tech_type,
                url
            }
        }
    }
`
